<template>
  <div class="row">
    <!--  -->
    <div class="col-md-12">
      <h1 class="text-center ">
        {{$route.params.name}}{{$route.params.name?',':""}}
      </h1>
      <!-- <music></music> -->
      <img :src="require('./../assets/img/19696025--0--6c1b6af_r.jpg')" class="img-responsive">
    </div>
  </div>
</template>
<script>
import router from "./../router/index.js";
export default {
  name: "Welcome",
  props: {
    //
  },
  data: function() {
    //
    return {};
  },
  mounted: function() {},
  methods: {},
  beforeRouteEnter(to, from, next) {
    //  confirm 
    // NOTE  `this`
    // 
    console.log(
      new Date().getHours() +
        ":" +
        new Date().getMinutes() +
        ":" +
        new Date().getSeconds() +
        ":" +
        "beforeRouteEnter"
    );
    next();
  },
  beforeRouteUpdate(to, from, next) {
    // 
    //  /foo/:id /foo/1  /foo/2 
    //  Foo 
    //  `this`
    console.log(
      new Date().getHours() +
        ":" +
        new Date().getMinutes() +
        ":" +
        new Date().getSeconds() +
        ":" +
        "beforeRouteUpdate"
    );
    next();
  },
  beforeRouteLeave(to, from, next) {
    // 
    //  `this`
    console.log(
      new Date().getHours() +
        ":" +
        new Date().getMinutes() +
        ":" +
        new Date().getSeconds() +
        ":" +
        "beforeRouteLeave"
    );
    next();
  }
};
</script>
