<template>
    <div class="container">
        <div class="row">

            <div class="col-md-4">
                <panel title=" v-btn-delay='10' ">
                    <button type="button" class="btn btn-success" v-btn-delay="10" @click="btnClick">
                        
                    </button>
                </panel>
            </div>

            <div class="col-md-4">
                <panel title="popover">
                    <a tabindex="0" v-popover class="btn btn-lg btn-danger" role="button" data-placement="bottom" data-toggle="popover" data-trigger="hover" title="" data-content="data-content" @show="popShow" @hide="popHide" @hidden="" @shown="">
                        
                    </a>
                    <p slot="footer">
                        Bootstrap, js
                    </p>
                </panel>
            </div>

            <div class="col-md-4">
                <panel title="tooltip">
                    <a href="javascript:;" title="v-tooltip.right" v-tooltip.right> tooltip</a><br>
                </panel>
            </div>
        </div>
        <panel>
            <h4 slot="header">
                <b></b> IMG:&lt;img v-random-img.square class="img-thumbnail"&gt;</h4>
            <div class="row">
                <div class="col-md-3"><img v-random-img.square class="img-thumbnail"></div>
                <div class="col-md-3"><img v-random-img.square class="img-thumbnail"></div>
                <div class="col-md-3"><img v-random-img.square class="img-thumbnail"></div>
                <div class="col-md-3"><img v-random-img.square class="img-thumbnail"></div>
            </div>
        </panel>

        <panel>
            <h4 slot="header">
                <b></b> :&lt;img v-random-img class="img-thumbnail"&gt;</h4>
            <div class="row">
                <div class="col-md-3"><img v-random-img class="img-thumbnail"></div>
                <div class="col-md-3"><img v-random-img class="img-thumbnail"></div>
                <div class="col-md-3"><img v-random-img class="img-thumbnail"></div>
                <div class="col-md-3"><img v-random-img class="img-thumbnail"></div>
            </div>
        </panel>
    </div>
</template>
<script>
import store from "./../store/index";
export default {
  name: "Point",
  store,
  mounted: function() {
    //
    store.commit("changeName");
  },
  methods: {
    changeName() {
      store.commit("changeName");
    },
    btnClick() {
      this.$notifyMessageLeft("");
    },
    popShow() {
      console.log("");
    },
    popHide() {
      console.log("");
    }
  }
};
</script>
<style scoped>

</style>


